      // loadingTable: false,
      // // 快速查詢帳號
      // searchAccountItems: {
      //   valid: false,
      //   account: '',
      //   rules: [] // *initLanguage*
      //   // rules: [
      //   //   v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || '需為英文或數字，不能為空格'
      //   // ]
      // },
      // // 載入篩選選單(每頁顯示、選擇狀態)
      // loadFilterMenuStatus: false,
      // // 選擇狀態
      // selectStatusItems: {
      //   selected: 'all',
      //   options: [] // *initLanguage*
      //   // options: [
      //   //   { text: '全部', value: 'all' },
      //   //   { text: '使用中', value: 0 },
      //   //   { text: '已鎖定', value: 1 }
      //   // ]
      // },
      // // 每頁顯示筆數
      // selectRowsPerPageItems: {
      //   selected: 25, // (若調整該值，需更新 rowsTableHeightClass 預設值)
      //   options: [] // *initLanguage*
      //   // options: [
      //   //   { text: '5筆', value: 5 },
      //   //   { text: '10筆', value: 10 },
      //   //   { text: '25筆', value: 25 },
      //   //   { text: '50筆', value: 50 }
      //   // ]
      // },
