          { text: '會員數', align: 'center', sortable: false },
          { text: '備註', align: 'center', sortable: false },
          { text: '登入錯誤次數', align: 'center', sortable: false },
          { text: '功能', align: 'center', sortable: false }
        ],
        contents: [],
        // 顯示BingoStar
        bingoStarShow: true,
        // 顯示皇家
        royalShow: true
      },
      // pagination: {
      //   currentPage: 1, // 當前頁數
      //   rowsPerPage: 25, // 每頁筆數(若調整該值，需更新 rowsTableHeightClass 預設值)
      //   totalItems: 0, // 總筆數
      //   totalVisible: 10 // 顯示最大可見頁數按鈕，其他會用 ... 顯示
      // }
    }
  },
  computed: {
    // ...mapGetters({
    //   getSuperAccountState: 'account-management/getSuperAccountState',
    //   getDashboardInfoState: 'account-management/getDashboardInfoState'
    // }),
    // pages () {
    //   if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
    //   return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    // }
  },
  watch: {
