    // initLanguage () {
    //   // 快速查詢帳號
    //   this.searchAccountItems.rules = [
    //     v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln('需為英文或數字，不能為空格')
    //   ]
    //
    //   // 選擇狀態
    //   this.selectStatusItems.options = [
    //     { text: this.$ln('全部'), value: 'all' },
